<template><section>
<h2 id="switch-kai-guan"><a class="header-anchor" href="#switch-kai-guan" aria-hidden="true">¶</a> Switch 开关</h2>
<p>表示两种相互对立的状态间的切换，多用于触发「开/关」。</p>
<h3 id="ji-chu-yong-fa"><a class="header-anchor" href="#ji-chu-yong-fa" aria-hidden="true">¶</a> 基础用法</h3>
<p>基础的开关用法</p>
<docs-demo-block> 
                                                <div class="source" slot="source"><fm-switch v-model="val"></fm-switch>
<fm-switch v-model="val2"></fm-switch>


</div>
                                                <p><code>v-model</code> 绑定的值是一个 <code>Boolean</code> 值。</p>
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">fm-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"val"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">fm-switch</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">fm-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"val2"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">fm-switch</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data () {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">val</span>: <span class="hljs-literal">false</span>,
                <span class="hljs-attr">val2</span>: <span class="hljs-literal">true</span>
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</docs-demo-block>
<h3 id="jin-yong-zhuang-tai"><a class="header-anchor" href="#jin-yong-zhuang-tai" aria-hidden="true">¶</a> 禁用状态</h3>
<p>开关的不可用状态</p>
<docs-demo-block> 
                                                <div class="source" slot="source"><fm-switch v-model="val3" disabled></fm-switch>
<fm-switch v-model="val4" disabled></fm-switch>


</div>
                                                <p>设置 <code>disabled</code> 属性即可。</p>
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">fm-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"val3"</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">fm-switch</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">fm-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"val4"</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">fm-switch</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data () {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">val3</span>: <span class="hljs-literal">true</span>,
                <span class="hljs-attr">val4</span>: <span class="hljs-literal">false</span>
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</docs-demo-block>
<h3 id="shu-xing"><a class="header-anchor" href="#shu-xing" aria-hidden="true">¶</a> 属性</h3>
<table class="table"><thead>
<tr>
<th style="text-align:left">参数</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">可选值</th>
<th style="text-align:left">默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">value</td>
<td style="text-align:left">绑定值</td>
<td style="text-align:left">boolean</td>
<td style="text-align:left">-</td>
<td style="text-align:left">false</td>
</tr>
<tr>
<td style="text-align:left">disabled</td>
<td style="text-align:left">是否禁用</td>
<td style="text-align:left">boolean</td>
<td style="text-align:left">—</td>
<td style="text-align:left">false</td>
</tr>
</tbody>
</table>
<h3 id="shi-jian"><a class="header-anchor" href="#shi-jian" aria-hidden="true">¶</a> 事件</h3>
<table class="table"><thead>
<tr>
<th style="text-align:left">事件名称</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">change</td>
<td style="text-align:left">当绑定值变化时触发的事件</td>
<td style="text-align:left">(event: Event)</td>
</tr>
</tbody>
</table>
</section></template>

<script>
    export default {
        data () {
            return {
                val: false,
                val2: true,
                val3: true,
                val4: false
            }
        }
    }
</script>


